import jwtAxios from '../../@crema/services/auth/jwt-auth/jwt-api';
import { fetchError, fetchStart, fetchSuccess } from './Common';
import { AppActions } from '../../types';
import { Dispatch } from 'redux';
import {
  SET_INITIAL_PATH,
  SettingsActionTypes,
  TOGGLE_NAV_COLLAPSED,
  GET_SETTINGS,
  SAVE_SETTINGS,
} from '../../types/actions/Settings.action';
// import { UPDATE_AUTH_USER } from 'types/actions/Auth.actions';

export const toggleNavCollapsed = () => ({ type: TOGGLE_NAV_COLLAPSED });

export const setInitialPath = (
  initialPath: string | undefined,
): SettingsActionTypes => ({
  type: SET_INITIAL_PATH,
  initialPath,
});

export const onGetDomains = () => {
  return async (dispatch: Dispatch<AppActions>) => {
    dispatch(fetchStart());
    try {
      const res = await jwtAxios.get(`/settings`);
      dispatch(fetchSuccess());
      dispatch({
        type: GET_SETTINGS,
        payload: res.data,
      });
    } catch (err: any) {
      console.log('error!!!!', err);
      dispatch(fetchError(err));
    }
  }
};

export const onSaveDomains = (refDomain: string, specDomain: string, clientDomain: string, cookieCheckDomain: string) => {
  return async (dispatch: Dispatch<AppActions>) => {
    dispatch(fetchStart());
    try {
      const res = await jwtAxios.post(`/settings`, {
        refDomain,
        specDomain,
        clientDomain,
        cookieCheckDomain
      });
      dispatch(fetchSuccess());
      dispatch({
        type: SAVE_SETTINGS,
        payload: res.data,
      });
    } catch (err: any) {
      console.log('error!!!!', err);
      dispatch(fetchError(err));
    }
  }
};

export const onUpdateAdminData = (data: { email: string, oldPassword: string, newPassword: string, confirmPassword: string }) => {
  return async (dispatch: Dispatch<AppActions>) => {
    dispatch(fetchStart());
    try {
      const res = await jwtAxios.post(`/settings/account`, data);
      dispatch(fetchSuccess());
      if (typeof res.data === 'string') {
        alert(res.data);
        return;
      }
      alert("Updated!")
      // dispatch({
      //   type: UPDATE_AUTH_USER,
      //   payload: res.data,
      // });
    } catch (err: any) {
      console.log('error!!!!', err);
      dispatch(fetchError(err));
    }
  }
};