import jwtAxios from '../../@crema/services/auth/jwt-auth/jwt-api';
import { fetchError, fetchStart, fetchSuccess } from './Common';
import { AppActions } from '../../types';
import { Dispatch } from 'redux';
import {
    GET_USERS_DATA,
    GET_USER_DATA,
    GET_ALL_USERS,
    BLOCK_USER,
    PIN_USER,
} from '../../types/actions/Users.action';

export const onGetUsersData = () => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            const res = await jwtAxios.get('/users/statistic');
            dispatch(fetchSuccess());
            dispatch({
                type: GET_USERS_DATA,
                payload: res.data,
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
};

export const onGetAllUsers = (search: string, page: number) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            const res = await jwtAxios.get(`/users/all?search=${search}&page=${page}`);
            dispatch(fetchSuccess());
            dispatch({
                type: GET_ALL_USERS,
                payload: res.data,
                search,
                page
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
};

