export const onGetUserData = (id: string) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            const res = await jwtAxios.get(`/users/${id}`);
            dispatch(fetchSuccess());
            dispatch({
                type: GET_USER_DATA,
                payload: res.data.user,
                clientDomain: res.data.clientDomain
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
}

export const onBlockUser = (uid: string, blocked: boolean) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            await jwtAxios.post(`/users/block`, {
                uid: uid,
                blocked: blocked
            });
            dispatch(fetchSuccess());
            dispatch({
                type: BLOCK_USER,
                uid
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
}

