import jwtAxios from '../../@crema/services/auth/jwt-auth/jwt-api';
import { fetchError, fetchStart, fetchSuccess } from './Common';
import { AppActions } from '../../types';
import { Dispatch } from 'redux';
import {
    SAVE_PROXY_DATA,
    GET_PROXY_DATA,
    GET_ALL_PROXIES,
    START_PROXY,
    STOP_PROXY,
    DELETE_PROXY,
    EDIT_PROXY,
} from '../../types/actions/Proxies.action';
import { SAVE_CURRENT_PAGE } from 'types/actions/Users.action';

export const onSaveProxyData = (saveData: { country: string, ip: string, port: string, fullname: string }) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            const res = await jwtAxios.post('/proxies', saveData);
            console.log(res)
            dispatch(fetchSuccess());
            dispatch({
                type: SAVE_PROXY_DATA
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
};

export const onGetProxyData = (id: number) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            const res = await jwtAxios.get('/proxies/' + id);
            dispatch(fetchSuccess());
            dispatch({
                type: GET_PROXY_DATA,
                payload: res.data,
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
};

export const onGetAllProxies = (search: string, page: number) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            const res = await jwtAxios.get(`/proxies?search=${search}&page=${page}`);
            dispatch(fetchSuccess());
            dispatch({
                type: GET_ALL_PROXIES,
                payload: res.data,
            });
            dispatch({
                type: SAVE_CURRENT_PAGE,
                search,
                page
            })
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
};

export const onStartProxy = (id: string) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            await jwtAxios.get(`/proxies/start/${id}`);
            dispatch(fetchSuccess());
            dispatch({
                type: START_PROXY,
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
}

export const onStopProxy = (id: string) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            await jwtAxios.get(`/proxies/stop/${id}`);
            dispatch(fetchSuccess());
            dispatch({
                type: STOP_PROXY,
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
}

export const onDeleteProxy = (id: string) => {
    return async (dispatch: Dispatch<AppActions>) => {
        dispatch(fetchStart());
        try {
            const res = await jwtAxios.delete(`/proxies/` + id);
            dispatch(fetchSuccess());
            dispatch({
                type: DELETE_PROXY,
                data: res.data
            });
        } catch (err: any) {
            console.log('error!!!!', err);
            dispatch(fetchError(err));
        }
    }
}

export const onEditProxy = (flag: boolean, id: string) => {
    return async (dispatch: Dispatch<AppActions>) => {
        try {
            dispatch({
                type: EDIT_PROXY,
                flag,
                id
            });
        } catch (err: any) {
            console.log('error!!!!', err);
        }
    }
}