import { Setting } from '../../types/models/Setting';
import {
  ROUTE_CHANGE,
  SET_INITIAL_PATH,
  SettingsActionTypes,
  TOGGLE_NAV_COLLAPSED,
  GET_SETTINGS,
} from '../../types/actions/Settings.action';

const initialSettings: Setting = {
  navCollapsed: false,
  initialPath: '/',
  refDomain: '',
  clientDomain: '',
  cookieCheckDomain: '',
  specDomain: ''
};

const Settings = (state = initialSettings, action: SettingsActionTypes) => {
  switch (action.type) {
    case ROUTE_CHANGE:
      return {
        ...state,
        navCollapsed: false,
      };

    case TOGGLE_NAV_COLLAPSED:
      return {
        ...state,
        navCollapsed: !state.navCollapsed,
      };

    case SET_INITIAL_PATH:
      return {
        ...state,
        initialPath: action.initialPath,
      };

    case GET_SETTINGS:
      return {
        ...state,
        refDomain: action.payload.refDomain,
        specDomain: action.payload.specDomain,
        clientDomain: action.payload.clientDomain,
        cookieCheckDomain: action.payload.cookieCheckDomain,
      }
    default:
      return state;
  }
};

export default Settings;
