import {
  CommonActionTypes,
  FETCH_ERROR,
  FETCH_START,
  FETCH_SUCCESS,
  GET_NOTIFICATION,
  SET_NOTIFICATION_IDS,
  HIDE_MESSAGE,
  SHOW_MESSAGE,
  TOGGLE_APP_DRAWER,
} from '../../types/actions/Common.action';
import { AppActions } from '../../types';
import { Dispatch } from 'redux';
import jwtAxios from '../../@crema/services/auth/jwt-auth/jwt-api';
import { GET_ALL_USERS_R, SET_REALTIME_DATA, GET_USERS_DATA, GET_USER_DATA } from 'types/actions/Users.action';
import { GET_ALL_PROXIES } from 'types/actions/Proxies.action';

export const fetchStart = (): CommonActionTypes => ({ type: FETCH_START });

export const fetchSuccess = (): CommonActionTypes => ({ type: FETCH_SUCCESS });

export const fetchError = (error: string): CommonActionTypes => ({
  type: FETCH_ERROR,
  error,
});

export const showMessage = (message: string): CommonActionTypes => ({
  type: SHOW_MESSAGE,
  message,
});

export const onToggleAppDrawer = (): CommonActionTypes => ({
  type: TOGGLE_APP_DRAWER,
});

export const hideMessage = (): CommonActionTypes => ({ type: HIDE_MESSAGE });

export const onGetNotificationData = (page: number, search: string) => {
  return async (dispatch: Dispatch<AppActions>) => {
    // dispatch(fetchStart());
    try {
      const res = await jwtAxios.get(`/notification?path=${window.location.pathname}&page=${page}&search=${search}`);
      // dispatch(fetchSuccess());
      dispatch({
        type: GET_NOTIFICATION,
        payload: res.data,
      });
      dispatch({
        type: SET_REALTIME_DATA,
        payload: res.data,
      });
      if (window.location.pathname.indexOf('/users/view') > -1) {
        dispatch({
          type: GET_USER_DATA,
          payload: res.data.user,
          clientDomain: res.data.clientDomain
        });
      }
      else if (window.location.pathname.indexOf('/users') > -1) {
        dispatch({
          type: GET_ALL_USERS_R,
          payload: res,
        });
      } else if (window.location.pathname.indexOf('/dashboard') > -1) {
        dispatch({
          type: GET_USERS_DATA,
          payload: res
        })
      } else if (window.location.pathname.indexOf('/proxies') > -1) {
        dispatch({
          type: GET_ALL_PROXIES,
          payload: res.data
        })
      }
    } catch (err: any) {
      console.log('error!!!!', err);
      dispatch(fetchError(err));
    }
  }
}

export const onClearNotificationData = () => {
  return async (dispatch: Dispatch<AppActions>) => {
    dispatch(fetchStart());
    try {
      await jwtAxios.get(`/notification/clear`);
      dispatch(fetchSuccess());
      dispatch({
        type: GET_NOTIFICATION,
        payload: [],
      });
    } catch (err: any) {
      console.log('error!!!!', err);
      dispatch(fetchError(err));
    }
  }
}

export const onSetShownNTs = (ids: string[]) => {
  return async (dispatch: Dispatch<AppActions>) => {
    try {
      dispatch({
        type: SET_NOTIFICATION_IDS,
        payload: ids,
      });
    } catch (err: any) {
      console.log('error!!!!', err);
    }
  }
}