import { Proxy } from "types/models/Proxy";

export const GET_PROXY_DATA = 'GET_PROXY_DATA';
export const SAVE_PROXY_DATA = 'SAVE_PROXY_DATA';
export const GET_ALL_PROXIES = 'GET_ALL_PROXIES';
export const START_PROXY = 'START_PROXY';
export const STOP_PROXY = 'STOP_PROXY';
export const DELETE_PROXY = 'DELETE_PROXY';
export const EDIT_PROXY = 'EDIT_PROXY';

export interface GetAllProxiesActions {
    type: typeof GET_ALL_PROXIES;
    payload: any | null;
}

export interface GetProxyDataActions {
    type: typeof GET_PROXY_DATA;
    payload: any | null;
}

export interface SaveProxyDataActions {
    type: typeof SAVE_PROXY_DATA;
}

export interface StartProxy {
    type: typeof START_PROXY;
}

export interface StopActions {
    type: typeof STOP_PROXY;
}

export interface DeleteProxy {
    type: typeof DELETE_PROXY;
    data: Proxy[];
}

export interface EditProxy {
    type: typeof EDIT_PROXY;
    flag: boolean;
    id: string;
}

export type ProxiesActionTypes =
    | GetAllProxiesActions
    | GetProxyDataActions
    | StartProxy
    | StopActions
    | DeleteProxy
    | EditProxy
    | SaveProxyDataActions
