import { User } from 'types/models/User';
import { AppActions } from '../../types';
import {
    GET_USERS_DATA,
    GET_ALL_USERS,
    GET_ALL_USERS_R,
    GET_USER_DATA,
    SET_REALTIME_DATA,
    BLOCK_USER,
    PIN_USER,
    SAVE_CURRENT_PAGE,
} from '../../types/actions/Users.action';

const INIT_STATE: {
    total: string;
    active: string,
    blocked: string,
    unattended: string,
    users: any[],
    userCount: number,
    user: User | null,
    search: string,
    page: number,
    clientDomain: string
} = {
    total: '0',
    active: '0',
    blocked: '0',
    unattended: '0',
    users: [],
    userCount: 0,
    user: null,
    search: '',
    page: 0,
    clientDomain: ''
};

const Users = (state = INIT_STATE, action: AppActions) => {
    switch (action.type) {
        case GET_USERS_DATA: {
            return {
                ...state,
                total: action.payload.data.total,
                active: action.payload.data.active,
                unattended: action.payload.data.unattended,
                blocked: action.payload.data.blocked,
            };
        }
        case GET_ALL_USERS: {
            return {
                ...state,
                users: action.payload.data.users,
                userCount: action.payload.data.userCount,
                search: action.search,
                page: action.page
            };
        }
        case GET_ALL_USERS_R: {
            return {
                ...state,
                users: action.payload.data.users,
                userCount: action.payload.data.userCount,
            };
        }
        case GET_USER_DATA: {
            return {
                ...state,
                user: action.payload,
                clientDomain: action.clientDomain
            };
        }
        case SAVE_CURRENT_PAGE: {
            return {
                ...state,
                page: action.page,
                search: action.search,
            };
        }
        case BLOCK_USER: {
            return {
                ...state,
            };
        }
        case SET_REALTIME_DATA: {
            return {
                ...state,
                unattended: action.payload.unattended,
                active: action.payload.active,

            };
        }
        case PIN_USER: {
            return {
                ...state,
            };
        }
        default:
            return state;
    }
};
export default Users;
