import { Proxy } from 'types/models/Proxy';
import { AppActions } from '../../types';
import {
    GET_ALL_PROXIES,
    GET_PROXY_DATA,
    STOP_PROXY,
    START_PROXY,
    DELETE_PROXY,
    EDIT_PROXY,
    SAVE_PROXY_DATA
} from '../../types/actions/Proxies.action';

const INIT_STATE: {
    proxies: Proxy[],
    proxyCount: number,
    proxy: Proxy | null,
    isShowProxyEdit: boolean,
    editProxyId: string,
} = {
    proxies: [],
    proxyCount: 0,
    proxy: null,
    isShowProxyEdit: false,
    editProxyId: ''
};

const Proxies = (state = INIT_STATE, action: AppActions) => {
    switch (action.type) {
        case GET_ALL_PROXIES: {
            return {
                ...state,
                proxies: action.payload.proxies,
                proxyCount: action.payload.proxyCount
            };
        }
        case GET_PROXY_DATA: {
            return {
                ...state,
                proxy: action.payload,
            };
        }
        case STOP_PROXY: {
            return {
                ...state,
            };
        }
        case START_PROXY: {
            return {
                ...state,
            };
        }
        case SAVE_PROXY_DATA: {
            return {
                ...state,
            }
        }
        case DELETE_PROXY: {
            return {
                ...state,
            };
        }
        case EDIT_PROXY: {
            return {
                ...state,
                isShowProxyEdit: action.flag,
                editProxyId: action.id
            }
        }
        default:
            return state;
    }
};
export default Proxies;
