export const SET_REALTIME_DATA = 'SET_REALTIME_DATA';
export const GET_USER_DATA = 'GET_USER_DATA';
export const GET_USERS_DATA = 'GET_USERS_DATA';
export const GET_ALL_USERS = 'GET_ALL_USERS';
export const BLOCK_USER = 'BLOCK_USER';
export const PIN_USER = 'PIN_USER';
export const GET_ALL_USERS_R = 'GET_ALL_USERS_R';
export const SAVE_CURRENT_PAGE = 'SAVE_CURRENT_PAGE';

export interface GetUsersActions {
    type: typeof GET_USERS_DATA;
    payload: any | null;
}

export interface GetAllUsersActions {
    type: typeof GET_ALL_USERS;
    payload: any | null;
    page: number;
    search: string;
}

export interface SavePageActions {
    type: typeof SAVE_CURRENT_PAGE;
    page: number;
    search: string;
}

export interface GetAllUsersRActions {
    type: typeof GET_ALL_USERS_R;
    payload: any | null;
}

export interface GetUserDataActions {
    type: typeof GET_USER_DATA;
    payload: any | null;
    clientDomain: string;
}

export interface BlockUserActions {
    type: typeof BLOCK_USER;
    uid: string;
}

export interface PinUserActions {
    type: typeof PIN_USER;
}

export interface SetRealtimeData {
    type: typeof SET_REALTIME_DATA;
    payload: any | null
}

export type UsersActionTypes =
    | GetUserDataActions
    | GetUsersActions
    | GetAllUsersActions
    | GetAllUsersRActions
    | BlockUserActions
    | PinUserActions
    | SetRealtimeData
    | SavePageActions
